<template>
  <div class="kanban page">
    <ul class="header">
      <li :class="{active: nowItem==='recieve'}" @click="toggleItem('recieve')">收到的任务</li>
      <li :class="{active: nowItem==='send'}" @click="toggleItem('send')">发起的任务</li>
      <li :class="{active: nowItem==='history'}" @click="toggleItem('history')">历史任务</li>
    </ul>
    <div class="scroll_content_box">
      <list-item v-for="item in taskData" :key="item.id" :to="`/kanban/detail/${item.id}`">
        <div slot="firstL">任务描述：</div>
        <div slot="firstR" class="tag" :class="getColor(item.task_priority)">{{item.task_priority | priority}}</div>
        <div slot="second">{{item.task_describe | desc}}</div>
        <div slot="thirdL">任务类型:</div>
        <div slot="thirdR">{{item.task_type_id}}</div>
        <div slot="fourthL">到期时间:</div>
        <div slot="fourthR">{{item.task_end_time | moment('YYYY-MM-DD HH:mm')}}</div>
      </list-item>
      <no-item v-if="taskData.length===0"></no-item>
      <div class="blank" v-else></div>
    </div>
    <bottom-btn to="/kanban/add">新增任务</bottom-btn>
  </div>
</template>

<script>
const PRIORITY = 'priority'
const INTERMEDIATE = 'intermediate'
const ELEMENTARY = 'elementary'
export default {
  name: 'Kanban',
  data () {
    return {
      nowItem: 'recieve',
      taskData: []
    }
  },
  methods: {
    toggleItem (item) {
      this.nowItem = item
      this.getTaskData()
    },
    getTaskData () {
      this.$rpc.call('funenc_xa_station.work_kanban', 'get_kanban_list', [], {
        item: this.nowItem
      }).then(res => {
        this.taskData = res
      }).catch(e => {
        console.log(e)
      })
    },
    getColor (v) {
      switch (v) {
        case PRIORITY:
          return 'gao'
        case INTERMEDIATE:
          return 'zhong'
        case ELEMENTARY:
          return 'di'
        default:
          break
      }
    }
  },
  filters: {
    priority (v) {
      switch (v) {
        case PRIORITY:
          return '高'
        case INTERMEDIATE:
          return '中'
        case ELEMENTARY:
          return '低'
        default:
          break
      }
    },
    desc (v) {
      return v || '暂无描述'
    }
  },
  mounted () {
    this.getTaskData()
  }
}
</script>

