    text-align: center;
    background: #fff;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 2;
    li {
      flex: 1;
      font-weight: 500;
    }
    .active{
      color: @linkColor;
      position: relative;
      &:after{
        content: '';
        background: @linkColor;
        height: 2 * @px1;
        position: absolute;
        bottom: 2 * @px1;
        left: 18.75%;
        right: 18.75%;
      }
    }
  }
  .scroll_content_box{
    overflow-y: scroll;
    -webkit-overflow-scrolling : touch;
    height: 100%;
    padding-top: 46 * @px1;
    box-sizing: border-box;
    .gao{
      color: @jujue;
      border: 1px solid @jujue;
    }
    .zhong{
      color: @bohui;
      border: 1px solid @bohui;
    }
    .di{
      color: @shenpi;
      border: 1px solid @shenpi;
    }
    .blank{
      height: 66 * @px1 !important;
    }
  }
}
</style>
